/*!  All style */

/* Shared basic style sheet */
@import './base/_base.scss';

/* Home page style */
@import './home/_home.scss';

/* Document page style */
@import './docs/_docs.scss';

/* About page style */
@import './about/_about.scss';


/** 
 * Page head
 */
.page-head {
    >.banner {
        padding: 90px 0;
    }

    .author {
        transition: transform 1s ease-in-out 1.5s;

        @media (min-width: 1200px) {
            transform: translateX(50%);
        }

        .name {
            position: relative;
            line-height: 3.5rem;
            transition: all .8s linear;
            transform: scale(.8);

            &::before,
            &::after {
                content: '';
                position: absolute;
                left: 0;
                width: 100%;
                height: 0;
                border-bottom: 2px solid #343a40;
                transform: scaleX(0);
                transform-origin: 0 0;
                transition: all 1s linear .5s;
            }

            &::after {
                transform-origin: 100% 0;
                bottom: 0;
            }

            &:hover::before, &:hover::after {
                transform: scaleX(0) !important;
            }
        }

        .constellation,
        .email {
            font-size: 1.25rem;
        }
    }

    .signature {
        text-align: left;
        color: #ced4da;
        font-size: 1.2rem;
        transition: all 1.6s ease-in 2s;
        max-height: 0;
        overflow: hidden;

        &:hover {
            color: black;
            transition: all .8s ease-in;
        }
    }

    >.main-nav {
        overflow: hidden;
        padding-bottom: .5rem;
        border-bottom: 1px solid #ddd;
        box-shadow: 0 .25rem .25rem rgba(22, 22, 22, .5);

        .nav-item, .nav-item::before {
            font-size: 1.5rem;
            text-decoration: none;
        }

        .nav-item {
            display: inline-block;
            position: relative;
            padding: 0 0 5px 0;
            color: #333;
            font-weight: lighter;
            transition: transform .6s linear .8s;
            transform: translateY(4rem);

            &::before {
                content: attr(txt);
                box-sizing: border-box;
                position: absolute;
                left: 0;
                top: 0;
                overflow: hidden;
                height: 100%;
                max-width: 0;
                padding: 0 0 4px 0;
                text-align: left;
                color: black;
                border-bottom: 1px solid black;
                transition: all .6s linear;
            }

            &:hover::before {
                max-width: 100%;
            }

            @media screen and (min-width: 992px) {
                &::before {
                    content: attr(txt-lg);
                }
            }
        }
    }

    /* animation */
    &.anim {

        .author {
            transform: translateX(0);

            .name {
                transform: scale(1);
                &::before,
                &::after {
                    transform: scaleX(1);
                }
            }
        }

        .signature {
            max-height: 10rem;
        }
        
        .nav-item {
            transform: translateY(0);
        }
    }
}

/** 
 * Page main
 */
.page-main {
    overflow-x: hidden;
    overflow-y: auto;
    padding: 2rem 1rem;
}

/** 
 * Page foot
 */
.page-foot {
    position: relative;
    padding: 3rem 0 5rem;
    color: white;
    background-color: #1b1e21;

    > .copyright {
        position: absolute;
        bottom: 1rem;
        left: 0;
        width: 100%;
    }
}